.Button {
  background-color: #534caf;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  box-sizing: border-box;
  width: 100%;
}

.Select {
  border: none;
  color: white;
  /* padding: 15px 32px; */
  text-align: center;
  text-decoration: none;
  display: flex;
  font-size: 16px;
  /* margin: 4px 2px; */
  cursor: pointer;
  box-sizing: border-box;
  width: 100%;
  /* min-width: 0px */
}

.NeighbouringHill {
  background-color: #3190bf;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 0px;
  cursor: pointer;
  display: block;
  box-sizing: border-box;
  width: 100%;
}

.Title {
  top: 50px;
  max-width: 1920px;
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
}

.Table {
  table-layout: fixed;
  margin: auto;
  border-collapse: separate;
  /* border-spacing: 50px 0; */
  width: 90%;
  text-align: justify;
}

.Modal {
  /*Position and width*/
  position: fixed;
  width: 90%;
  height: fit-content;
  
  /*NOTE: If you want your modal's position to be absolute or fixed, you should also include:*/
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  
  /*Centering horizintally*/
  margin: auto;

  /*Purely visual stuff*/
  border: 2px solid #ccc;
  border-radius: 3px;
  background-color: ghostwhite;
  text-align:center;
}
